.aboutCared {
  width: 55%;
  height: 20%;
  float: left;
  margin: 6% 8%;
}
.portfolioHolder {
  margin: 3% 8%;
}
.mediaCared {
  width: 20%;
  height: 10%;
  float: left;
  margin: 6% 0;
}
.portfolioCard {
  maxwidth: 275;
  height: 20%;
  float: left;
  margin: 6% 7%;
}
.portfolioMedia {
  height: 170px;
}

.portfolioTitle {
  color: #fca05f;
  font-weight: bold;
}
.typewriter {
  font-weight: bold;
  font-size: 28px;
  color: #fca05f;
  align-self: center;
  margin-top: 3%;
}
.react-rotating-text-cursor {
  animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s
    infinite;
}

@keyframes blinking-cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

html,
body {
  /*height: 100%;*/
  /*width: 100%;*/
  background-image: url("./images/ep_naturalwhite.png");
  font-family: "Times New Roman", Times, serif;
}

.aboutme {
  height: auto;
  overflow-y: auto;
}
.container {
  width: 100vw;
  height: 100%;
  position: relative;
  margin: 0%;
}
a.menu-link {
  text-decoration: none;
  color: #fca05f;
  font-size: 15px;
}
.menu-container {
  background-color: white;
  height: 100%;
  position: relative;
}
.menu {
  width: 30%;
  float: left;
  margin-top: 2%;
}

.myname {
  width: 20%;
  height: 200%;
  color: white;
  background-color: #fca05f;
  /*font-size: 19px;*/
  font-weight: bold;
  text-align: center;
  float: left;
  left: 0%;

  font-size: 28px;
  margin-right: 48%;

  padding: 0px 7px 0px 7px;
}

.media-holder,
.image-holder {
  background-color: white;
}
li {
  display: inline;
  list-style: none;
  margin-right: 6%;
}
.media-list {
  padding-left: 0;
  align-content: center;
}
.media-link img {
  width: 50px;
  height: 50px;
  border-radius: 0px;
  margin-left: 5px;
}

footer {
  left: 0;
  bottom: 0;
  width: 100%;
  display: block;
  text-align: center;
  color: white;
  height: 100%;
  clear: left;
}
.aboutme > h1,
.media > h3 {
  color: #ffa300;
  font-family: Georgia, Times, "Times New Roman", serif;
}
.horazontal-line {
  border: 1.5px solid #cccccc;
}

.l2 {
  margin-left: 2%;
  color: #ffa300;
}

footer p {
  text-align: center;
}

.my-image {
  width: 25%;
  border-radius: 50%;
  float: left;
  margin-left: 2%;
  margin-right: 2%;
}
a.menu-link:link {
  text-decoration: none;
  color: white;
  font-size: 20px;
  font-weight: bold;
}
a.menu-link:visited {
  text-decoration: none;
}
a.menu-link:hover {
  text-decoration: none;
  font-size: 27px;
  font-weight: bold;
  color: #777777;
}
/*this is for link in protifolioCard*/
a.menu-lin:link {
  text-decoration: none;
  font-size: 20px;
}
a.menu-lin:visited {
  text-decoration: none;
  /*color: #7e7979;*/
}
a.menu-lin:hover {
  text-decoration: none;
  /*color: #FCA05F;*/
  font-weight: bold;
}
a.menu-link:active {
  text-decoration: none;
  color: white;
}
button {
  padding: 12px 35px;
  background-color: #f7bd94;
  border-color: #f7bd94;
  color: black;
  font-size: 16px;
  margin-right: 10px;
}
button:hover {
  background-color: #f4d7c2;
}
.img:hover {
  opacity: 0.5;
}
.media {
  padding: 3%;
}
.aboutme {
  padding: 0% 2%;
}

input[type="text"],
input[type="email"] {
  width: 100%;
  height: 30%;
  padding: 1.5% 0%;
  border: 0.5px solid #cccccc;
  margin-top: 1%;
}
textarea {
  border: 0.5px solid #cccccc;
  width: 100%;
}

input[type="submit"] {
  padding: 12px 35px;
  background-color: #fca05f;
  color: white;
  font-size: 16px;
  margin-top: 10px;
}
input[type="submit"]:hover {
  background-color: #fee1cd;
  color: #fca05f;
}

/*NEW MENU*/
.topnav {
  overflow: hidden;
  background-color: white;
}

.topnav a {
  float: left;
  display: block;
  color: #fca05f;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  font-weight: bold;
}

.topnav a:hover {
  background-color: #fca05f;
  color: white;
}

