/*!******** Between  980 - 769 PX**********************!*/
@media screen and (min-width: 769px) and (max-width: 980px) {
  .myname {
    width: 25%;
    margin-right: 35%;
  }
  .menu {
    width: 37%;
    float: left;
    border: 1px solid black;
  }
  .aboutCared {
    width: 45%;
  }
  .mediaCared {
    width: 30%;
    height: 10%;
    float: left;
    margin: 6% 0;
  }
}

/*!******** Between  768-641 PX**********************!*/
@media screen and (min-width: 641px) and (max-width: 768px) {
  .myname {
    width: 32%;
    margin-right: 18%;
  }

  .menu {
    width: 47%;
    float: left;
  }

  .aboutCared {
    width: 78%;
  }

  .mediaCared {
    width: 78%;
    height: 10%;
    float: left;
    margin: 0% 8% 3%;
  }

  .my-image {
    width: 45%;
    margin-left: 3%;
    margin-right: 3%;
  }
  /*.hey{*/
  /*text-align: justify;*/
  /*}*/
  .media-link img {
    width: 65px;
    height: 65px;
    /*border-radius: 0px;*/
    margin-right: 11px;
    align-content: center;
  }
  .media-list {
    margin-left: 15%;
  }
}
/*!******** Below  640 PX**********************!*/
@media only screen and (max-width: 640px) {
  .my-image {
    width: 100%;
    margin-left: 3%;
    margin-right: 3%;
  }
  .myname {
    width: 100%;
    /*margin-right: 18%;*/

    margin-right: 0%;
  }
  .menu {
    width: 100%;
    margin-right: 0%;
    align-content: start;
  }
  .aboutCared {
    width: 78%;
  }
  .mediaCared {
    width: 78%;
    height: 10%;
    float: left;
    margin: 6% 8% 1%;
  }
  a.menu-link {
    margin-left: 0%;
  }
  .menu-list li {
    margin-left: 0%;
    display: inline-block;
  }
  a.menu-lin:link {
    font-size: 15px;
  }
  .my-image {
    margin-bottom: 3%;
  }
  .typewriter {
    margin-left: 5%;
    align: center;
    margin-top: 5%;
  }
}
